body {
  background: linear-gradient(to top, #283E51, #0A2342);
  height: 100vh;
  color: white;
  text-align: center;
}



.centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
/* BUTTON STYLING EDITED FROM  https://codepen.io/fossheim/pen/RwPeVKG*/
    button {
      display: block;
      cursor: pointer;
      outline: none;
      border: none;
      background-color: lightgrey;
      width: 400px;
      height: 70px;
      border-radius: 30px;
      font-size: 1.5rem;
      font-weight: 600;
      background-size: 100% 100%;
      box-shadow: 0 0 0 7px lightgrey inset;
      margin-bottom: 15px;
      margin-top: 15px;
    }
    button:hover {
      background-image: linear-gradient(
        145deg,
        transparent 10%,
        darkgrey 10% 20%,
        transparent 20% 30%,
        darkgrey 30% 40%,
        transparent 40% 50%,
        darkgrey 50% 60%,
        transparent 60% 70%,
        darkgrey 70% 80%,
        transparent 80% 90%,
        darkgrey 90% 100%
      );
      animation: background 3s linear infinite;
    }

    @keyframes background {
      0% {
        background-position: 0 0;
      }
      100% {
        background-position: 400px 0;
      }
    }

input {
  width: 390px;
  height: 70px;
  border-radius: 20px;
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center
}

label {
  border-radius: 20px;
  font-size: 1.5rem;
  font-weight: 600;
  margin-right: 15px;
}

.inputPair {
  display: flex;
  align-items: center;
}

ul {
  padding: 0
}

.goalDisplay {
  margin: 20px;
  background-color: gray;
  border-radius: 20px;
  min-width: 100px;
  padding: 0 10px;
}
.goalContainer {
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  justify-content: space-evenly;
}